@use "../global/variables.scss";
@use "../global/mixins.scss";

.main-product {
  @include mixins.container-mobile;
  padding-top: 19px;
  padding-bottom: 25px;
  border-top: 1px solid #ebebeb;
}

.main-product--more {
  padding-top: 25px;
  border-bottom: 1px solid #ebebeb;
  background-image: none;
}

.main-product__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px;
}

.main-product__image-link {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-product__image {
  display: block;
  transform: translateY(10px);
}

.main-product__image--small {
  transform: translateY(16px);
}

.main-product__image--first {
  transform: translateY(4px);
}

.main-product__title-link {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0 0 14px;
  padding: 0;
  text-decoration: none;
  color: variables.$black;
}

.main-product__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.main-product__description-title {
  font-size: 12px;
  line-height: 16px;
  color: variables.$dark-grey;
}

.main-product__description-details {
  font-size: 12px;
  line-height: 16px;
  color: variables.$dark-grey;
  margin: 0;
}

.main-product__link-more {
  text-decoration: none;
  color: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &:hover {
    .main-product__plus {
      opacity: 0.6;
    }
  }
}

.main-product__plus {
  margin-bottom: 23px;
}

.main-product__title-link--more {
  margin-bottom: 6px;
}

.main-product__description {
  margin: 0 0 20px;
  font-size: 12px;
}

@media (max-width: variables.$mobile-width-only) {
  .main-product__title-link {
    max-width: 77px;
    display: inline-block;
  }

  .main-product__title-link--more {
    max-width: 100%;
  }

  .main-product__image-link {
    width: 50%;
  }

  .main-product__wrapper-text {
    width: 50%;
  }
}

@media (min-width: variables.$tablet-width) {
  .main-product {
    padding: 0 36px 39px;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 81px;
      background-color: variables.$basic-grey;
      z-index: -1;
    }
  }

  .main-product__image {
    width: 168px;
    height: 200px;
  }

  .main-product__image--small {
    width: 162px;
    height: 173px;
    transform: translateY(10px);
  }

  .main-product__image--first {
    transform: translateY(10px);
  }

  .main-product--more {
    padding-top: 47px;
    padding-bottom: 35px;
    align-self: end;
    background-color: variables.$white;
    border: 2px solid #f2f2f2;
    background-image: none;
    margin-top: 80px;
  }

  .main-product__plus {
    width: 60px;
    height: 60px;
    margin-bottom: 49px;
  }

  .main-product__wrapper {
    display: block;
    margin-top: auto;
    margin-bottom: 40px;
  }

  .main-product__wrapper-text {
    display: flex;
    flex-direction: column;
  }

  .main-product__image-link {
    align-items: center;
    margin-bottom: 45px;
  }

  .main-product__list {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      height: 1px;
      left: 0;
      right: 0;
      bottom: -9px;
      background-color: #cdcdcd;
    }

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  .main-product__title-link {
    text-align: center;
    font-size: 20px;
    line-height: 24px;
    max-width: 154px;
    align-self: center;
    margin-bottom: 21px;
  }

  .main-product__title-link--more {
    margin-bottom: 23px;
  }

  .main-product__description-title {
    font-size: 16px;
  }

  .main-product__description-details {
    font-size: 16px;
  }

  .main-product__description {
    text-align: center;
    max-width: 167px;
    margin: 0;
    font-size: 16px;
  }

  .main-product__link-more {
    margin-bottom: 87px;
  }
}

@media (min-width: variables.$desktop-width) {
  .main-product__image-link {
    margin-bottom: 43px;
  }

  .main-product__title-link {
    margin-bottom: 20px;
  }

  .main-product__image {
    display: block;
    transform: translateY(0);
  }

  .main-product__image--small {
    transform: translateY(0);
  }

  .main-product__image--first {
    transform: translateY(0);
  }
}
